<template>
  <div :class="{ 'dark-mode': darkMode }" class="container mt-5">
    <!-- Motivational Quote Section -->
    <div class="quote-box position-absolute top-0 start-0 p-2 bg-light rounded shadow-sm">
      <p class="mb-0 text-muted">{{ currentQuote }}</p>
    </div>

    <!-- Dark Mode Toggle -->
    <div class="text-end mb-3">
      <button @click="toggleDarkMode" class="btn btn-dark">
        {{ darkMode ? "Light Mode" : "Dark Mode" }}
      </button>
    </div>

    <h2 class="text-center mb-4">Task Manager</h2>

    <!-- Completion Progress Bar -->
    <div class="progress mb-3">
      <div class="progress-bar" role="progressbar" :style="{ width: completionPercentage + '%' }">
        {{ completionPercentage }}% Completed
      </div>
    </div>

    <!-- Search and Filter -->
    <div class="row mb-3">
      <div class="col-md-6">
        <input v-model="searchQuery" @input="fetchTasks" class="form-control" placeholder="Search tasks..." />
      </div>
      <div class="col-md-6">
        <select v-model="selectedPriority" @change="fetchTasks" class="form-select">
          <option value="">All Priorities</option>
          <option value="Low">Low</option>
          <option value="Medium">Medium</option>
          <option value="High">High</option>
        </select>
      </div>
    </div>

    <!-- Task Input Form -->
    <div class="card p-4 shadow-sm mb-4">
      <h5 class="card-title text-center">Add a New Task</h5>
      <form @submit.prevent="addTask">
        <div class="mb-3">
          <label class="form-label">Task Title</label>
          <input v-model="newTaskTitle" class="form-control" placeholder="Enter task title" required />
        </div>
        <div class="mb-3">
          <label class="form-label">Deadline (Required)</label>
          <input type="datetime-local" v-model="newTaskDeadline" class="form-control" required />
        </div>
        <div class="mb-3">
          <label class="form-label">Priority</label>
          <select v-model="newTaskPriority" class="form-select">
            <option value="Low">Low</option>
            <option value="Medium" selected>Medium</option>
            <option value="High">High</option>
          </select>
        </div>
        <button type="submit" class="btn btn-primary w-100">Add Task</button>
      </form>
    </div>

    <!-- Task List -->
    <div class="card shadow-sm">
      <div class="card-header">
        <h5 class="mb-0">Your Tasks</h5>
      </div>
      <ul class="list-group list-group-flush">
        <li v-for="task in tasks" :key="task.id" class="list-group-item d-flex justify-content-between align-items-center">
          <div>
            <span :class="{ 'text-decoration-line-through': task.completed }">
              <strong>{{ task.title }}</strong> ({{ task.priority }})
            </span>
            <br>
            <small class="text-muted">{{ formatDate(task.deadline) }}</small>
          </div>
          <div>
            <button @click="toggleTask(task)" class="btn btn-sm btn-success me-2">
              {{ task.completed ? "Undo" : "Complete" }}
            </button>
            <button @click="deleteTask(task.id)" class="btn btn-sm btn-danger">
              Delete
            </button>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      newTaskTitle: "",
      newTaskDeadline: "",
      newTaskPriority: "Medium",
      tasks: [],
      darkMode: false,
      searchQuery: "",
      selectedPriority: "",
      quotes: [
        "No Pain No Gain",
        "Get Busy Living Or Get Busy Dying.",
        "Something Is Better Than Nothing.",
        "Hope Is A Good Thing.",
        "Discipline Is The Key .",
        "Limits Only Exist In Your Mind."
      ],
      currentQuote: "Get Busy Living Or Get Busy Dying"
    };
  },
  computed: {
    completionPercentage() {
      const total = this.tasks.length;
      const completed = this.tasks.filter(task => task.completed).length;
      return total > 0 ? Math.round((completed / total) * 100) : 0;
    }
  },
  methods: {
    async fetchTasks() {
      const params = {};
      if (this.searchQuery) params.search = this.searchQuery;
      if (this.selectedPriority) params.priority = this.selectedPriority;

      const res = await axios.get("/tasks", { params });
      this.tasks = res.data;
    },
    async addTask() {
      if (!this.newTaskTitle || !this.newTaskDeadline) {
        alert("Please enter both task title and deadline!");
        return;
      }
      try {
        const res = await axios.post("/tasks", {
          title: this.newTaskTitle,
          deadline: this.newTaskDeadline,
          priority: this.newTaskPriority,
        });
        this.tasks.push(res.data);
        this.newTaskTitle = "";
        this.newTaskDeadline = "";
        this.newTaskPriority = "Medium";
      } catch (error) {
        console.error("Error adding task:", error.response ? error.response.data : error.message);
      }
    },
    async toggleTask(task) {
      task.completed = !task.completed;
      await axios.put(`/tasks/${task.id}`, { completed: task.completed });
    },
    async deleteTask(id) {
      await axios.delete(`/tasks/${id}`);
      this.tasks = this.tasks.filter(task => task.id !== id);
    },
    formatDate(deadline) {
      return deadline ? new Date(deadline).toLocaleString() : "No deadline";
    },
    toggleDarkMode() {
      this.darkMode = !this.darkMode;
      document.body.classList.toggle("dark-mode", this.darkMode);
      localStorage.setItem("darkMode", this.darkMode);

    },
    changeQuote() {
      this.currentQuote = this.quotes[Math.floor(Math.random() * this.quotes.length)];
    }
  },
  mounted() {
    this.fetchTasks();
    setInterval(this.changeQuote, 30000);

    const savedDarkMode = localStorage.getItem("darkMode") === "true";
    this.darkMode = savedDarkMode;
    document.body.classList.toggle("dark-mode", savedDarkMode);
  }
};
</script>

<style>
.quote-box {
  font-size: 14px;
  max-width: 200px;
 }

 .dark-mode {
  background-color: #121212 !important;
  color: white !important;
}
.dark-mode .card {
  background-color: #1e1e1e;
  color: white;
}
.dark-mode .list-group-item {
  background-color: #2a2a2a;
  color: white;
}
.dark-mode .btn-dark {
  background-color: #ffcc00;
  color: black;
}
.dark-mode body {
  background-color: #121212;
}

</style>
